<template>
  <div class="examples">
    <h1>Näited</h1>
    <div class="buttons">
      <button v-for="example in examples" v-on:click="currentExample = example" v-bind:class="['example-button', { active: currentExample === example }]"> {{ example }}</button>
    </div>

    <keep-alive>
      <component v-bind:is="currentExampleComponent"></component>
    </keep-alive>
  </div>
</template>

<script>
import Markdowneditor from '@/components/Markdowneditor'
import myModal from '@/components/Modal'
import KeyboardItem from '@/components/KeyboardItem'
import Button from '@/components/Button'
import Accordion from '@/components/Accordion'

export default {
  components: {
    Markdowneditor,
    myModal,
    KeyboardItem,
    Button,
    Accordion
  },
  data() {
    return {
      examples: ['Markdowneditor', 'myModal', 'KeyboardItem', 'Button', 'Accordion'],
      currentExample: 'Markdowneditor'
    }
  },
  computed: {
    currentExampleComponent: function() {
      return this.currentExample
    }
  }
}
</script>

<style scoped>
  .slide-fade-enter-active {
    transition: all .3s ease;
  }
  .slide-fade-leave-active {
    transition: all .4s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }
  .slide-fade-enter, .slide-fade-leave-to {
    transform: translateX(10px);
    opacity: 0;
  }

  .example-button.active {
    background: #2c885e;
  }
  
  span {
    transition: all 3s;
    animation: fadeOutRight 4s;
    animation-fill-mode: both;
  }

    @keyframes fadeOutRight {
      0% {
          opacity: 1;
          transform: translateX(0);
      }
      100% {
          opacity: 0;
          transform: translateX(20px);
      }
    }

  h1 {
    color: #000;
    padding: 1rem 0;
  }

  .buttons {
    display: flex;
    justify-content: center;
    margin-bottom: 2rem;
  }

  button {
    display: block;
    background: #42b983;
    border: none;
    outline: none;
    padding: .5rem 1.25rem;
    color: white;
    margin: 0 .5rem;
  }

  button:hover {
    background: #2c885e;
  }

  @media (max-width: 768px) {
    .buttons {
      flex-direction: column;
    }

    button {
      margin: .5rem auto;
      width: 90%;
    }
  }
</style>