<template>
<div>
    <p>Sisestage tekst vasakul olevasse halli kasti ning seejärel näete antud teksti kuvatuna paremal kastis.</p>
    <transition name="slide-fade">
    <div>
        <div id="editor">
            <textarea :value="input" @input="update"></textarea>
            <div class="output" v-html="compiledMarkdown"></div>
        </div>
        <div>
            <a class="btn code" href="http://vuejs.org/v2/examples/index.html" target="_blank">Kood</a>
        </div>
    </div>
    </transition>
</div>
</template>

<script>
export default {
    name: 'Markdowneditor',
    data() {
        return {
            input: 'hello'
        }
    },
    computed: {
        compiledMarkdown: function() {
            return this.input
        }
    },
    methods: {
        update: function (e) {
            this.input = e.target.value
        }
    }
}
</script>

<style scoped>

    body, #editor {
    margin: 0;
    height: 100%;
    font-family: 'Helvetica Neue', Arial, sans-serif;
    color: #333;
    }

    #editor {
        display: flex;
        margin-bottom: 3rem;
    }

    textarea, #editor div {
    display: inline-block;
    width: 49%;
    height: 100%;
    vertical-align: top;
    box-sizing: border-box;
    padding: 0 20px;
    }

    textarea {
    border: none;
    border-right: 1px solid #ccc;
    resize: none;
    outline: none;
    background-color: #f6f6f6;
    font-size: 14px;
    font-family: 'Monaco', courier, monospace;
    padding: 20px;
    }

    .btn.code {
        margin-top: 2rem;
        padding: 1rem 2rem;
        background: #42b983;
        color: #fff;
    }

    @media (max-width: 768px) {
        #editor {
            flex-direction: column;
        }

        #editor > * {
            width: 100%;
            height: 100%;
        }

        .output {
            margin-top: 1rem;
            text-align: left;
        }
    }
</style>