<template>
    <transition name="slide-fade">
    <div>
        <div>
            <p>Vajutage allolevale elemendile nimega "Accordion", et avada peidetud sisu.</p>
            <article class="message" :class="accordionClasses">
                <div class="message-header" @click="toggleAccordion">
                    Accordion
                    <i class="arrow down"></i>        
                </div>
                <div class="message-body">
                    <div class="message-content">
                        Lorem ipsum dolor sit amet, consectetur adipiscing elit. 
                    </div>
                </div>
            </article>
        </div>
        <div>
            <a class="btn code" href="https://jsfiddle.net/465fhdw0/" target="_blank">Kood</a>
        </div>
    </div>
    </transition>
</template>

<script>
export default {
    name: 'Accordion',
    data() {
        return {
            isOpen: false
        }
    },
    methods: {
        toggleAccordion: function() {
            this.isOpen = !this.isOpen;
        }
    },
    computed: {
        accordionClasses: function() {
            return {
                'is-closed': !this.isOpen,
                'is-primary': this.isOpen,
                'is-dark': !this.isOpen
            };
        }
    }
}
</script>


<style scoped>
    .message {
        max-width: 500px;
        margin-left: auto;
        margin-right: auto;
    }

    .message-header {
        position: relative;
        cursor: pointer;
        background: #42b983;
        padding: 1rem .5rem;
        color: #fff;
        font-size: 1.2rem;
    }

    .arrow {
        position: absolute;
        right: 20%;
        /* top: 26px; */
        top: 20px;
        border: solid white;
        border-width: 0 3px 3px 0;
        display: inline-block;
        padding: 5px;
        transition: 0.3s ease-in-out;
    }

    .down {
        transform: rotate(45deg);
        /* transform: rotate(-135deg); */
    }

    .is-primary .down{
        top: 26px;
        transform: rotate(-135deg);
    }

    .message-body   {
        padding: .5rem;
        /* max-height: 10em; */
        overflow: hidden;
        transition: 0.3s ease all;
        background: #ececec;
    }

    .is-closed .message-body {
        max-height: 0;
        background: transparent;
    }

    .message-content {
        padding: 20px;
    }

    .btn.code {
        margin-top: 2rem;
        padding: 1rem 2rem;
        background: #42b983;
        color: #fff;
    }
</style>